.SideBar{
    display: flex;
    flex-direction: column;
    position: relative;
    padding-top: 4rem;
    transition: all 300ms ease;
}

.logo{
    display: flex;
    font-weight: bold;
    font-size: 22px;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    height: 4%;
}

.logo>img{
    width: 3rem;
    height: 3rem;
}
.logo>span>span{
    color: var(--primary);
}

.menu{
    margin-top: 4rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.menuItem{
    display: flex;
    align-items: center;
    gap: 1rem;
    height: 2.5rem;
    margin-left: 2rem;
    transition: all 300ms ease;
    font-size: 12px;
    border-radius: 0.7rem;
}
.menuItem:hover{
    cursor: pointer;
}

.active{
    background: var(--activeItem);
    margin-left: 0;
}
.active::before{
    content: '';
    width: 8px;
    height: 100%;
    background: var(--primary);
    margin-right: calc(1rem - 8px);
}

@media screen and (max-width: 1200px){
    .menuItem>span{
        display: none;
    }
    .logo{
        display: none;
    }
}